import React, { Component } from 'react'

import './Input.css';

import { connect } from 'react-redux';
import { Button, Form, FormGroup, Label, Input, FormText } from 'reactstrap';

class input extends Component {
    render() {

        let taskName = '';
        let descriptionValue = '';
        let priorityValue = 0;

        const taskNameValue = (event) => {
            taskName = event.target.value;
        }

        const description = (event) => {
            descriptionValue = event.target.value;
        }

        const priority = (event) => {
            priorityValue = Number(event.target.value);

        }

        console.log(typeof (priorityValue))
        console.log(priorityValue)

        return (
            <div className="input-container-super">
                <div className="hint-container">
                    <p className="hint">{this.props.message}</p>
                </div>

                <div className="formstyle">
                    <div style={{
                        display: 'flex',
                        flexDirection: 'row',
                        justifyContent: 'space-between'
                    }}>
                        <Input style={{
                            width: '49%',
                            borderStyle: 'none',
                            boxShadow: 'inset 4px 4px 11px 1px #dcdcdc47',
                            borderRadius: '7px'
                        }} placeholder="Title" onChange={taskNameValue} type="text" name="title" required="true"></Input>

                        <Input style={{
                            width: '46%',
                            borderStyle: 'none',
                            boxShadow: 'inset 4px 4px 11px 1px #dcdcdc47',
                            borderRadius: '7px'
                        }} onChange={priority} type="select" name="select">
                            <option value="4">Priority</option>
                            <option className="high" value="1">High</option>
                            <option className="mid" value="2">Medium</option>
                            <option className="low" value="3">Low</option>
                        </Input>
                    </div>
                    <textarea placeholder="Description" className="des" onChange={description}></textarea>
                    <button className="add-button" onClick={() => this.props.addTask(priorityValue, taskName, descriptionValue)} >+</button>
                </div>




            </div >
        )
    }
}






const mapStateToProps = state => {
    return {
        message: state.message
    }
}

const mapDispatchToProps = (dispatch) => {

    return {
        addTask: (p, n, d) => dispatch({
            type: 'add',
            task: {
                priority: p,
                taskName: n,
                des: d,
                isDone: false
            }
        })
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(input);



